<template>
    <div>
        <div class="size-icon" @click="drawer=true">
            <i class="el-icon-chat-line-square"></i>
        </div>

        <el-drawer
            :visible.sync="drawer"
            :direction="direction"
            :append-to-body="true">
            <!-- 评论输入框 -->
            <div class="comments">
                <div style="margin-bottom: 10px;margin-left: 10px;margin-right: 10px;">
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 2, maxRows: 4}"
                        placeholder="请输入内容"
                        v-model="textarea">
                    </el-input>
                </div>
                
                <div class="button">
                    <el-button class="btn" type="info" round plain>评论</el-button>
                </div>
            </div>

            <!-- 展示这篇文章的所有评论 -->
            <div class="allComments" >
                <ul v-for="(tableDatas,index) in tableData" :key="index">
                    <li>
                        &nbsp; {{tableData[parseInt(commentsIndex)][index]}} &nbsp; 
                    </li>
                </ul>
            </div>

        </el-drawer>
    </div>
</template>

<script>
    import bus from '../bus';

    export default {
      data() {
        return {
            drawer: false,
            direction: 'rtl',
            // 评论输入框的值
            textarea:'',
            commentsIndex:'0',
            tableData: [ // 要展示在卡片上的数据 
                ["ddd","bbb"],
                ["地方","二分法"]
            ],
        };
      },
      methods: {
        getIndex() {

        },

      },
      mounted() {
        // setInterval(function () {
        bus.$on('commentsVal',(index) => {
            console.log(index);
            this.commentsIndex = index;
            console.log(this.commentsIndex);
        })
      },
    };
  </script>

  <style>
    .size-icon {
        font-size: 32px;
        position: absolute;
        right: 50px;
    }

    .size-icon:hover {
        cursor:pointer;
    }

    .input {
        width: 100%;
        height: 100px;
    }

    .btn {
        position: absolute;
        right: 10px;
    }

    .allComment {
        max-height: 590;
        overflow: auto;
    }
  </style>


