<template>
    <el-container>
        <!-- 头部dao'hang'la -->
        <el-header>
            <el-row>
                <el-col :span="8">
                    <div class="grid-content bg-purple">&nbsp;</div>
                </el-col>

                <el-col :span="8">
                    <div class="grid-content bg-purple-light">&nbsp;</div>
                </el-col>

                <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-row>
                            <el-col :span="14"><div class="grid-content bg-purple-light">&nbsp;</div></el-col>

                            <el-col :span="5">
                                <div class="grid-content bg-purple" onclick="window.open('https://github.com/taiguiwang')">
                                    <i id="github">GitHub</i>
                                </div>
                            </el-col>
                            
                            <el-col :span="5">
                                <div class="grid-content bg-purple" onclick="window.open('https://gitee.com/taiguiwang')">
                                    <i id="gitee" >Gitee</i>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </el-col>
            </el-row>
        </el-header>

        <el-container>
            <!-- 侧边导航栏 -->
            <el-aside width="200px">
                <el-col class="index">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="index" class="grid-content bg-purple">首页</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="blog">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="blog" class="grid-content bg-purple">博客</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="write">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="write" class="grid-content bg-purple">写</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="contact">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="contact" class="grid-content bg-purple">联系</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col>
                    <el-col :span="24"><div>&nbsp;</div></el-col>
                </el-col>

                <el-col>
                   <el-col :span="24"><div>&nbsp;</div></el-col>
                </el-col>
            </el-aside>

            <!-- 内容展示 -->
            <el-main>
                <el-button @click="push">Contact</el-button>
            </el-main>
        </el-container>

        <el-header>
            © 2022 TaiGui Wang
        </el-header>
    </el-container>
</template>

<script>
    import Push from 'push.js';
    
    export default {
        name: 'Index',
        methods: {
            // 路由跳转
            index:function() {
                this.$router.push('/');
            },
            blog:function() {
                this.$router.push('/Blog');
            },
            write:function() {
                this.$router.push('/Write');
            },
            contact:function() {
                this.$router.push('/Contact');
            },
            push() { 
                Push.create("Hello world!", {
                    // body 选项是通知的内容
                    body: "今天xxx中奖100万",
                    // icon 选项是通知的图片
                    // timeout 选项是通知停留时间
                    timeout: 4000
                });
            }
        }
    }

</script>

<style>
    .index {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .index:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .blog {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .blog:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .write {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .write:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .contact {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .contact:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    #github:hover {
        cursor:pointer;
        color: chocolate;
    }

    #gitee:hover {
        cursor:pointer;
        color: chocolate;
    }


    /* element */
    .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 100px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 80px; /**main 上下移动 */
  }
  
</style>


