<template>
    <el-container>
        <!-- 头部dao'hang'la -->
        <el-header>
            <el-row>
                <el-col :span="8">
                    <div class="grid-content bg-purple">&nbsp;</div>
                </el-col>

                <el-col :span="8">
                    <div class="grid-content bg-purple-light">&nbsp;</div>
                </el-col>

                <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-row>
                            <el-col :span="14"><div class="grid-content bg-purple-light">&nbsp;</div></el-col>

                            <el-col :span="5">
                                <div class="grid-content bg-purple" onclick="window.open('https://github.com/taiguiwang')">
                                    <i id="github">GitHub</i>
                                </div>
                            </el-col>
                            
                            <el-col :span="5">
                                <div class="grid-content bg-purple" onclick="window.open('https://gitee.com/taiguiwang')">
                                    <i id="gitee" >Gitee</i>
                                </div>
                            </el-col>
                        </el-row>
                    </div>
                </el-col>
            </el-row>
        </el-header>

        <el-container>
            <!-- 侧边导航栏 -->
            <el-aside width="200px">
                <el-col class="index">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="index" class="grid-content bg-purple">首页</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="blog">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="blog" class="grid-content bg-purple">博客</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="write">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="write" class="grid-content bg-purple">写</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col class="contact">
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                    <el-col :span="16"><div @click="contact" class="grid-content bg-purple">联系</div></el-col>
                    <el-col :span="4"><div class="grid-content bg-purple">&nbsp;</div></el-col>
                </el-col>

                <el-col>
                    <el-col :span="24"><div>&nbsp;</div></el-col>
                </el-col>

                <el-col>
                   <el-col :span="24"><div>&nbsp;</div></el-col>
                </el-col>
            </el-aside>

            <!-- 展示所有博客文章 -->
            <el-main>
                <!-- 卡片 -->
                <ul v-for="(tableDatas,index) in tableData" :key="index">
                    <el-button type="text" @click="blogTextIndex(index); dialogTableVisible=true">
                        <el-card class="box-card">
                            <div class="content" >
                                <div>
                                    <span class="title">{{tableData[index].name}}</span>
                                </div>
                                
                                <div>
                                    <span>content</span>
                                </div>
                            </div>
                        </el-card>
                    </el-button>
                    
                    
                </ul>

                <el-dialog class="dialog_f" :visible.sync="dialogTableVisible">
                    <div class="dialog">
                        <p>&nbsp; {{tableData[blogIndex].name}} &nbsp;</p>
                        <div>内容</div>
                        <div><Comments /></div>
                    </div>
                </el-dialog>                
            </el-main>
        </el-container>
        <div><Comments /></div>
        <el-header>
            © 2022 TaiGui Wang
        </el-header>
    </el-container>
</template>

<script>
    import Comments from './Comments.vue';
    import bus from '../bus';

    export default {
        name: 'Index',
        components:{
            Comments,
        },
        data() {
            return{
                tableData: [ // 要展示在卡片上的数据
                    {
                        name:'hello'
                    },
                    {
                        name:'world'
                    },
                    {
                        name:'大家好！'
                    },
                    
            ],
            // 文章索引
            blogIndex:0,
            // 控制弹窗
            dialogTableVisible: false,
        }
        },
        methods: {
            // 路由跳转
            index:function() {
                this.$router.push('/');
            },
            blog:function() {
                this.$router.push('/Blog');
            },
            write:function() {
                this.$router.push('/Write');
            },
            contact:function() {
                this.$router.push('/Contact');
            },
            // 获取的是点击哪篇文章的索引（index）
            blogTextIndex:function(index) {
                this.blogIndex = index;
                // 改变 el-dialog 标签上面的样式
                var s = document.querySelector('.el-dialog');
                s.style.marginTop = "5vh"
                // 向Comments组件传参
                console.log("传参" + this.blogIndex)
                bus.$emit('commentsVal',this.blogIndex);
            }
        },
        
    }

</script>

<style>
    /* 弹窗 */
    .dialog {
        width: 100%;
        height: 598px;
        max-height: 590;
        overflow: auto;
    }

    .dialog_f {
        width: 100%;
        height: 100%;
    }

    /* 卡片 */
    .box-card {
        width: 800px;
        height: 100px;
        letter-spacing: 1px;
    }

    .title {
        font-size: 16px;
        font-weight: 800;
    }

    .content div{
        display: flex;
        margin-bottom: 10px;
    }

    /* 导航栏 */
    .index {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .index:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .blog {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .blog:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .write {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .write:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    .contact {
        font-size: 16px;
        letter-spacing: 1.5px;
        font-weight: 600;
    }

    .contact:hover {
        cursor:pointer;
        color: chocolate;
        background-color: rgb(209, 209, 209);
    }

    #github:hover {
        cursor:pointer;
        color: chocolate;
    }

    #gitee:hover {
        cursor:pointer;
        color: chocolate;
    }


    /* element */
    .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  
  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 100px;
  }
  
  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 80px; /**main 上下移动 */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
</style>


