<template>
    <div>
        <el-input v-model="input" placeholder="请输入标题"></el-input>

        <mavon-editor 
            v-model="content" 
            ref="md" 
            @change="change" 
            style="min-height: 600px"
        />
  
        <el-button type="primary" @click="submit">提交</el-button>
    </div>
  </template>
  
  
  
  <script>
  // 导入组件 及 组件样式
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'

  export default {
    // 注册
    components: {
        mavonEditor,
    },
    data() {
        return {
            content:'', // 输入的markdown
            html:'',    // 及时转的html
            input:''  // 标题值
        }
    },

    methods: {
        // 所有操作都会被解析重新渲染
        change(value, render){
            // render 为 markdown 解析后的结果[html]
            this.html = render;
        },
        // 提交
        submit(){
            console.log(this.content);
            console.log(this.html);

            // title的值
            console.log("input:" + this.input);

            fetch('/api/v1/blog',{
                method:'post',
                body:JSON.stringify({
                    'textHtml':this.html,
                    'title':this.input,
                }),
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(res => {
                console.log(res);
            })

        }
    },

  }
  </script>



